.section__services {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.services__container {
    width: 90vw;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.services {
    border-radius: 10px;
    width: 20%;
    padding: 20px;
    -webkit-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
    -moz-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
    box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
}

.services:hover {
    -webkit-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
    box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
}

.service__figure {
    margin: auto;
    border: 2px solid #000000;
    background: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    width: 100px;
    border-radius: 50px;
}

.services h3 {
    display: flex;
    justify-content: center;
}

.service__figure--img {
    height: 60px;
}

.service__figure--img:hover {
    height: 66px;
}