.header {
    height: 70px;
    background: #1a3c40;
    display: flex;
    justify-content: center;
}

.nav {
    width: 90vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__figure {
    margin: 0;
    height: 50px;
}

.nav__figure--img {
    height: 50px;
}

.nav__ul {
    display: flex;
    justify-content: space-between;
    list-style: none;
}

.nav__ul a {
    margin: 0px 8px;
    padding: 8px 16px;
    border-radius: 8px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    color: #e4eddb;
}

.nav__ul a:hover {
    padding: 8px 16px;
    border-radius: 8px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    text-decoration: none;
    background: #20484d;
}