.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.hero__container {
    width: 90vw;
    padding: 6% 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    background-image: url('https://i.imgur.com/ORSsCh2.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position-x: right;
}

.hero__container p {
    border-radius: 15px;
    background: rgba(48, 118, 114, 0.6);
    padding: 16px 28px;
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    color: white;
}