
.section__publications {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.publications__container {
    width: 90vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
}

.publications {
    border-radius: 14px;
    width: 45%;
    min-width: 350px;
    margin-bottom: 30px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    -webkit-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
    -moz-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
    box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.05);
}

.publications:hover {
    -webkit-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
    box-shadow: 0px 3px 8px 4px rgba(0,0,0,0.2);
}

.publications__figure {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 300px;
    z-index: 2;
    overflow: hidden;
}

.publications__figure--img {
    width: 100%;
    transition: 1s;
}

.publications__figure--img:hover {
    transform: scale(1.2);
    transition: 1s;
}

.publications__text {
    padding: 20px;
    height: 200px;
    background: #ffffff;
    background-repeat: no-repeat;
    background-size: 300px;
    background-position-x: right;
    overflow: hidden;
    z-index: 3;
}
