.footer {
    background: #144d53;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.contact {
    width: 90vw;
    height: 400px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    background-image: url('https://i.imgur.com/ORSsCh2.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position-x: right;
}

.contact__div {
    width: 25%;
    height: 80px;
    padding: 30px;
    font-size: 18px;
    font-weight: 700;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
}

.contact__div p {
    border-radius: 12px;
    padding: 20px;
    height: 180px;
    font-size: 20px;
    font-weight: 700;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    color: white;
    background: rgba(48, 118, 114, 0.6);
}

.media {
    width: 50%;
    height: 180px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
}

.media__figure {
    height: 100px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.media__figure--img {
    height: 84px;
    width: 84px;
    padding: 3px;
}

.media__figure--img:hover {
    height: 90px;
    width: 90px;
    padding: 0;
}

.author {
    background-color: #1a3c40;
    width: 100%;
    height: 70px;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

h2 {
    font-size: 28px;
    font-weight: 700;
    font-family: Arial, Helvetica, sans-serif;
    color: #307672;
}
